html[data-theme='dark'] {
    --main-font: 'Montserrat', sans-serif;
    --main-color: #B6452C;
    --sub-color: #301B28;
    --up-text-color: #fff;
    --text-color: #fff;
    --background-color: #021C1E;
}

html[data-theme='light'] {
    --main-font: 'Montserrat', sans-serif;
    --main-color: #66A5AD;
    --sub-color: #6C2D2C;
    --up-text-color: #fff;
    --text-color: #000;
    --background-color: #F1DCC9;
}

body {
    background-color: var(--background-color);
    transition: .2s linear;
}

ul {
    list-style-type: none;
    padding-left: 0;
}

li {
    margin-left: 10px;
    color: var(--text-color);
    margin-top: 10px;
    font-size: 17px;
}

h2, p {
    font-weight: 400;
    color: var(--text-color);
}

a {
    color: var(--text-color);
    text-decoration: none;
    cursor: pointer;
}

.justify-box {
    display: flex;
}

header {
    box-shadow: 0 15px 30px 10px rgba(0, 0, 0, .1);
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 2px solid var(--main-color);
    border-radius: 0 0 10px 10px;
    position: fixed;
    top: 0;
    left: 50%;
    transform: translate(-50%);
    transition: all .4s ease-out;
    z-index: 1;
    background-color: var(--sub-color);
}

nav {
    padding-left: 30px;
}

nav span {
    color: var(--up-text-color);
    font-size: 20px;
    text-transform: uppercase;
    margin-right: 25px;
    transition: color linear .2s;
}

nav span:hover {
    color: var(--main-color);
}

header svg {
    margin-right: 60px;
    cursor: pointer;
}

header svg:hover * {
    fill: var(--main-color);
}

.light {
    display: none;
}

.aside-bar {
    position: fixed;
    padding-left: 30px;
    height: 100vh;
    width: 20%;
    opacity: .9;
    overflow-y: scroll;
}

.aside-bar h2 {
    margin-top: 70px;
    line-height: 1;
}

.accorLink:hover + .arrow {
    opacity: 1;
}

.arrow {
    width: 20px;
    height: 100%;
    opacity: 0;
    transition: .2s ease;
}

.arrow * {
    fill: var(--text-color);
}

.classUL, .themeUL {
    display: none;
}

main {
    display: flex;
    justify-content: right;
}

main p {
    padding-top: 70px;
    width: 75%;
}